import ReactLoading from 'react-loading';
import { Component } from 'react';
import App from './content/main';
const Load = 'https://cdn.yajtpg.now.sh';
class AppLoad extends Component {
    state = {
        someData: [],
        isLoading: true
      }
    
      componentDidMount() {
        setTimeout(() => { this.someFetch() }, 250);
      }
    
      someFetch = () => {
        fetch(Load)
          .then(res => res.json())
          .then(res => this.setState({ 
            someData: res,
            isLoading: false })
        )
      }
      
    render() {
        return (
            <div>
                {this.state.isLoading ? (
                    <div id="div1">
                        <center><ReactLoading type={"spin"} color={"white"} /></center>
                    </div>
                ) : (
                        <App />
                    )}
            </div>
        );
    }
}
export default AppLoad;